
import { defineComponent, ref } from 'vue'

type Item = { id: number, fliped: boolean}

export default defineComponent({
  setup () {
    const listData = ref([
      {
        id: 1,
        fliped: false
      },
      {
        id: 2,
        fliped: false
      },
      {
        id: 3,
        fliped: false
      },
      {
        id: 4,
        fliped: false
      },
      {
        id: 5,
        fliped: false
      },
      {
        id: 6,
        fliped: false
      },
      {
        id: 7,
        fliped: false
      },
      {
        id: 8,
        fliped: false
      },
      {
        id: 9,
        fliped: false
      }
    ])
    const animating = ref(false)
    return {
      listData,
      animating
    }
  },
  methods: {
    handleReset (item: Item, node: HTMLElement) {
      if (item.fliped) {
        node.style.transform = 'rotateY(0deg)'
        item.fliped = false
      } else {
        node.style.transform = 'rotateY(180deg)'
        item.fliped = true
      }
    },
    // 翻转一个
    handleFlipCurrent (item: Item) {
      if (this.animating) return
      this.animating = true
      const node = this.$refs[`item_${item.id}`] as HTMLElement
      const fn = () => {
        node.removeEventListener('transitionend', fn)
        this.animating = false
      }
      node.addEventListener('transitionend', fn)
      this.handleReset(item, node)
    },
    // 全部一起翻转
    handleFlipAll () {
      if (this.animating) return
      this.animating = true
      for (let i = 0, len = this.listData.length; i < len; i++) {
        const node = this.$refs[`item_${this.listData[i].id}`] as HTMLElement
        if (i === 0) {
          const fn = () => {
            node.removeEventListener('transitionend', fn)
            this.animating = false
          }
          node.addEventListener('transitionend', fn)
        }
        this.handleReset(this.listData[i], node)
      }
    },
    // 依次翻转
    async handleFlipQueue () {
      if (this.animating) return
      this.animating = true
      for (let i = 0, len = this.listData.length; i < len; i++) {
        await new Promise((resolve) => {
          const node = this.$refs[`item_${this.listData[i].id}`] as HTMLElement
          const fn = () => {
            node.removeEventListener('transitionend', fn)
            resolve(1)
            if (i === len - 1) {
              this.animating = false
            }
          }
          node.addEventListener('transitionend', fn)
          this.handleReset(this.listData[i], node)
        })
      }
    },
    // 一起洗牌（推荐）
    async handleShuffle () {
      if (this.animating) return
      this.animating = true
      const flipData = this.listData.filter(item => item.fliped !== false)
      if (flipData.length) {
        // 保证所有的卡牌翻转回初始状态
        flipData.forEach(item => (item.fliped = false))
        const node = this.$refs[`item_${flipData[0].id}`] as HTMLElement
        await new Promise((resolve) => {
          const fn = function () {
            node.removeEventListener('transitionend', fn)
            resolve(1)
          }
          node.addEventListener('transitionend', fn)
        })
      }
      // 之后将所有的卡牌进行收缩
      for (let i = 0, len = this.listData.length; i < len; i++) {
        if (i === 4) continue
        const node = this.$refs[`item_${this.listData[i].id}`] as HTMLElement
        // 计算每个卡片 x 和 y 偏移量
        const x = 220 - (i % 3) * 220
        const y = 280 - ~~(i / 3) * 280
        node.style.transform = `translate3d(${x}px, ${y}px, 0)`
      }
      // 等待收缩完毕
      await new Promise((resolve) => {
        const node = this.$refs[`item_${this.listData[0].id}`] as HTMLElement
        const fn = function () {
          node.removeEventListener('transitionend', fn)
          setTimeout(() => {
            resolve(1)
          }, 200)
        }
        node.addEventListener('transitionend', fn)
      })
      // 进行释放
      for (let i = 0, len = this.listData.length; i < len; i++) {
        if (i === 4) continue
        const node = this.$refs[`item_${this.listData[i].id}`] as HTMLElement
        if (i === 0) {
          const fn = () => {
            node.removeEventListener('transitionend', fn)
            this.animating = false
          }
          node.addEventListener('transitionend', fn)
        }
        node.style.transform = 'translate3d(0, 0, 0)'
      }
    },
    // 依次洗牌
    async handleShuffleQueue () {
      if (this.animating) return
      this.animating = true
      const flipData = this.listData.filter(item => item.fliped !== false)
      if (flipData.length) {
        // 保证所有的卡牌翻转回初始状态
        flipData.forEach(item => (item.fliped = false))
        const node = this.$refs[`item_${flipData[0].id}`] as HTMLElement
        await new Promise((resolve) => {
          const fn = function () {
            node.removeEventListener('transitionend', fn)
            resolve(1)
          }
          node.addEventListener('transitionend', fn)
        })
      }
      for (let i = 0, len = this.listData.length; i < len; i++) {
        if (i === 4) continue
        await new Promise((resolve) => {
          const node = this.$refs[`item_${this.listData[i].id}`] as HTMLElement
          const fn = function () {
            node.removeEventListener('transitionend', fn)
            node.style.transitionDuration = '0.6s'
            resolve(1)
          }
          node.addEventListener('transitionend', fn)
          // 计算每个卡片 x 和 y 偏移量
          const x = 220 - (i % 3) * 220
          const y = 280 - ~~(i / 3) * 280
          node.style.transitionDuration = '0.2s'
          node.style.transform = `translate3d(${x}px, ${y}px, 0)`
        })
      }
      for (let i = 0, len = this.listData.length; i < len; i++) {
        if (i === 4) continue
        await new Promise((resolve) => {
          const node = this.$refs[`item_${this.listData[i].id}`] as HTMLElement
          const fn = () => {
            node.removeEventListener('transitionend', fn)
            node.style.transitionDuration = '0.6s'
            resolve(1)
            if (i === len - 1) {
              this.animating = false
            }
          }
          node.addEventListener('transitionend', fn)
          node.style.transitionDuration = '0.2s'
          node.style.transform = 'translate3d(0, 0, 0)'
        })
      }
    }
  }
})
